const Landing = () => {
  const [code, setCode] = useState(javascriptDefault);
  const [customInput, setCustomInput] = useState("");
  const [outputDetails, setOutputDetails] = useState(null);
  const [processing, setProcessing] = useState(null);
  const [theme, setTheme] = useState("cobalt");
  const [language, setLanguage] = useState(languageOptions[33]);
  const [htmlPrev, setHtmlPrev] = useState(false);

  const enterPress = useKeyPress("Enter");
  const ctrlPress = useKeyPress("Control");
  var [toast, setToast] = useState(false);
  const toastRef = useRef("toastRef");
  var [codeHtml, setCodeHtml] = useState(htmlDefault);
  var [drawer, setDrawer] = useState(false);

  const options = [
    {value: '', text: '--Choose--'},
    {value: 0, text: 'Html'},
    {value: 1, text: 'HtmlBS3'},
    {value: 2, text: 'HtmlBS4'},
    {value: 3, text: 'HtmlBS5'},
  ];

  const [selected, setSelected] = useState(options[0].value);

  const onSelectChange = (sl) => {
    console.log("selected Option...", sl);
    setLanguage(sl);
  };

  useEffect(() => {
    if (enterPress && ctrlPress) {
      console.log("enterPress", enterPress);
      console.log("ctrlPress", ctrlPress);
      handleCompile();
    }
  }, [ctrlPress, enterPress]);
  useEffect(() => {
    var myToast = toastRef.current
    var bsToast = bootstrap.Toast.getInstance(myToast)
    
    if (!bsToast) {
        // initialize Toast
        bsToast = new Toast(myToast, {autohide: false})
        // hide after init
        bsToast.hide()
        setToast(false)
    }
    else {
        // toggle
        toast ? bsToast.show() : bsToast.hide()
    }
  });
  const onChange = (action, data) => {
    switch (action) {
      case "code": {
        setCode(data);
        break;
      }
      default: {
        console.warn("case not handled!", action, data);
      }
    }
  };
  const handleCompile = () => {
    setProcessing(true);
    const formData = {
      language_id: language.id,
      // encode source code in base64
      source_code: btoa(code),
      stdin: btoa(customInput),
    };
    const options = {
      method: "POST",
      url: process.env.REACT_APP_RAPID_API_URL,
      params: { base64_encoded: "true", fields: "*" },
      headers: {
        "content-type": "application/json",
        "Content-Type": "application/json",
        "X-RapidAPI-Host": process.env.REACT_APP_RAPID_API_HOST,
        "X-RapidAPI-Key": process.env.REACT_APP_RAPID_API_KEY,
      },
      data: formData,
    };

    axios
      .request(options)
      .then(function (response) {
        console.log("res.data", response.data);
        const token = response.data.token;
        checkStatus(token);
      })
      .catch((err) => {
        let error = err.response ? err.response.data : err;
        // get error status
        let status = err.response.status;
        console.log("status", status);
        if (status === 429) {
          console.log("too many requests", status);

          showErrorToast(
            `Quota of 100 requests exceeded for the Day! Please read the blog on freeCodeCamp to learn how to setup your own RAPID API Judge0!`,
            10000
          );
        }
        setProcessing(false);
        console.log("catch block...", error);
      });
  };

  const checkStatus = async (token) => {
    const options = {
      method: "GET",
      url: process.env.REACT_APP_RAPID_API_URL + "/" + token,
      params: { base64_encoded: "true", fields: "*" },
      headers: {
        "X-RapidAPI-Host": process.env.REACT_APP_RAPID_API_HOST,
        "X-RapidAPI-Key": process.env.REACT_APP_RAPID_API_KEY,
      },
    };
    try {
      let response = await axios.request(options);
      let statusId = response.data.status?.id;

      // Processed - we have a result
      if (statusId === 1 || statusId === 2) {
        // still processing
        setTimeout(() => {
          checkStatus(token);
        }, 2000);
        return;
      } else {
        setProcessing(false);
        setOutputDetails(response.data);
        showSuccessToast(`Compiled Successfully!`);
        console.log("response.data", response.data);
        return;
      }
    } catch (err) {
      console.log("err", err);
      setProcessing(false);
      showErrorToast();
    }
  };

  function handleThemeChange(th) {
    const theme = th;
    console.log("theme...", theme);

    if (["light", "vs-dark"].includes(theme.value)) {
      setTheme(theme);
    } else {
      defineTheme(theme.value).then((_) => setTheme(theme));
    }
  }
  useEffect(() => {
    defineTheme("brilliance-black").then((_) =>
      setTheme({ value: "brilliance-black", label: "Brilliance Black" })
    );
  }, []);

  function handleHtmlPreview() {
      setHtmlPrev(true);
      /*setTimeout(()=>{
        setCode(htmlDefault);
        setHtmlPrev(false);
      },2000);*/
      //console.log(toastRef.current);
      //bootstrap.Toast.getInstance(toastRef.current).show();
      setTimeout(()=>{
        setCodeHtml(code);
      },200);
      showHideToast();
  }
  function showHideToast(){
    setToast(toast => !toast);
  }
  function resetHtml(event){
    event.preventDefault();
    var type = event.target.value;
    setSelected(type);
    setTimeout(()=>{
      setHtmlPrev(true);
      setCode(type==0?htmlDefault:(type==1?htmlDefaultB3:(type==2?htmlDefaultB4:(type==3?htmlDefaultB5:htmlDefault))));
      setHtmlPrev(false);
    },200);
  }
  function handleHtmlExit() {
    setToast(toast => !toast);
    setHtmlPrev(false);
  }
  function copyResult(){
    console.log(atob(outputDetails.stdout));
    setTimeout(()=>{
      setCode(atob(outputDetails.stdout));
    },200);
  }
  function showHideDrawer(){
    setDrawer(drawer => !drawer);
  }
  function exportZip(){
    var zip = new JSZip();
    zip.file("index.html",code);
    zip.generateAsync({type:"blob"})
    .then(function(content) {
        // see FileSaver.js
        saveAs(content, "example.zip");
    });
  }
  const showSuccessToast = (msg) => {
    toast.success(msg || `Compiled Successfully!`, {
      position: "top-right",
      autoClose: 1000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };
  const showErrorToast = (msg, timer) => {
    toast.error(msg || `Something went wrong! Please try again.`, {
      position: "top-right",
      autoClose: timer ? timer : 1000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  return (
    <>
    {drawer&&(<div id="drawer-navigation" className="fixed z-40 h-screen p-4 overflow-y-auto bg-dark w-80 dark:bg-gray-800" tabIndex="-1" aria-labelledby="drawer-navigation-label">
          <h5 id="drawer-navigation-label" className="text-base font-semibold text-gray-500 uppercase dark:text-gray-400">Menu</h5>
          <button type="button" data-drawer-dismiss="drawer-navigation" aria-controls="drawer-navigation" className="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 absolute top-2.5 right-2.5 inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"  onClick={showHideDrawer} >
              <svg aria-hidden="true" className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd"></path></svg>
              <span className="sr-only">Close menu</span>
          </button>
          <div>
            <ul style={{padding:'0'}}>
              <li className="flex flex-row text-white justifyContent:flex-start alignItems:flex-start">
                  <p className="w-[60%]">Language</p><div className="w-[55%]"><LanguagesDropdown onSelectChange={onSelectChange} /></div>
              </li>
              <li className="flex flex-row text-white justifyContent:flex-start alignItems:flex-start">
                <p className="w-[60%]">Theme</p><div className="w-[55%]"><ThemeDropdown handleThemeChange={handleThemeChange} theme={theme} /></div>
              </li>
              <li className="flex flex-row text-white justifyContent:flex-start alignItems:flex-start">
                <p className="w-[60%]">Preview</p><div className="w-[75%]"><button
                      onClick={handleHtmlPreview}
                      className="border-2 border-black z-10 rounded-md shadow-[5px_5px_0px_0px_rgba(0,0,0)] px-4 py-2 hover:shadow transition duration-200 bg-white text-black flex-shrink-0">Html Preview</button></div>
              </li>
              <li className="flex flex-row text-white justifyContent:flex-start alignItems:flex-start">
                <p className="w-[60%]">HTML</p><div className="w-[75%]"><select className="custom-select text-black px-4 py-2 w-[100%]" value={selected} onChange={resetHtml}>
                  {options.map(option => (
                    <option key={option.value} value={option.value}>
                      {option.text}
                    </option>
                  ))}
                </select></div>
              </li>
              <li className="flex flex-row text-white justifyContent:flex-start alignItems:flex-start">
                <p className="w-[60%]">Export Zip</p><div className="w-[75%]"><button
                      onClick={exportZip}
                      className="border-2 border-black z-10 rounded-md shadow-[5px_5px_0px_0px_rgba(0,0,0)] px-4 py-2 hover:shadow transition duration-200 bg-white text-black flex-shrink-0">Export Zip</button></div>
              </li>
              </ul>
          </div>
        </div>)}
    {!htmlPrev&&(<div><button className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 dark:bg-blue-600 dark:hover:bg-blue-700 focus:outline-none dark:focus:ring-blue-800" type="button" data-drawer-target="drawer-navigation" data-drawer-show="drawer-navigation" aria-controls="drawer-navigation" onClick={showHideDrawer}>
        Menu
        </button>
        </div>)}
      <ToastContainer
        position="top-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />

      {!htmlPrev&&(<div className="h-4 w-full bg-gradient-to-r from-pink-500 via-red-500 to-yellow-500"></div>)}
      {!htmlPrev&&(<div className="flex flex-row">
      </div>)}
      {!htmlPrev&&(<div className="flex flex-row m-auto grid grid-cols-1 grid-rows-2 md:grid-cols-2 md:grid-rows-1 space-x-4 px-4 py-4">
        <div className="flex flex-col w-full h-full justify-start items-end">
          <CodeEditorWindow
            code={code}
            onChange={onChange}
            language={language?.value}
            theme={theme.value}
          />
        </div>

        <div className="right-container flex flex-shrink-0 w-[90%] flex-col" style={{backgroundColor:'white'}}>
          <OutputWindow outputDetails={outputDetails} />
          <div className="flex flex-col items-end">
            <CustomInput
              customInput={customInput}
              setCustomInput={setCustomInput}
            />
            <div className="flex flex-row items-end m-auto grid grid-cols-1 grid-rows-1 md:grid-cols-2">
            <div className="px-4 py-2">
            <button
              onClick={copyResult}
              className="border-2 border-black z-10 rounded-md shadow-[5px_5px_0px_0px_rgba(0,0,0)] px-4 py-2 hover:shadow transition duration-200 bg-white flex-shrink-0">Copy Php Result To Editor</button>
            </div>
            <div className="px-4 py-2">
            <button
              onClick={handleCompile}
              disabled={!code}
              className={classnames(
                "mt-4 border-2 border-black z-10 rounded-md shadow-[5px_5px_0px_0px_rgba(0,0,0)] px-4 py-2 hover:shadow transition duration-200 bg-white flex-shrink-1",
                !code ? "opacity-50" : ""
              )}
            >
              {processing ? "Processing..." : "Compile and Execute"}
            </button>
            </div>
            </div>
          </div>
          {outputDetails && <OutputDetails outputDetails={outputDetails} />}
        </div>
      </div>)}
      {!htmlPrev&&(<Footer />)}
      {/*!htmlPrev&&(<button className="btn btn-success" onClick={() => setToast(toast => !toast)}>
            Toast {toast?'hide':'show'}
              </button>)*/}
      <div className="toast" role="alert" aria-live="assertive" aria-atomic="true" ref={toastRef}>
        <div className="toast-body">
          Hello, world! This is a toast message.
          <div className="mt-2 pt-2 border-top">
            <button type="button" className="btn btn-primary btn-sm" onClick={handleHtmlExit}>Take action-Back Home</button>
            <button type="button" className="btn btn-secondary btn-sm" data-bs-dismiss="toast" onClick={handleHtmlExit}>Close</button>
          </div>
        </div>
      </div>
      {htmlPrev&&(<div dangerouslySetInnerHTML={{ __html:codeHtml}} />)}
    </>
  );
};
export default Landing;
